.home-wall {
  background-image: url(/img/packages/ladakh-bike-1600x900.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: center center;
  height: 540px;
  height: 80vh;
  position: relative;
  text-align: center; }
  .home-wall:before {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 0; }
  .home-wall .wall-title {
    color: #FFFFFF;
    background-color: rgba(0, 0, 0, 0.1);
    position: absolute;
    right: 1rem;
    bottom: 1rem;
    font-size: 1.125rem;
    padding: 0.1rem 0.3rem; }
  .home-wall .wall-content {
    line-height: 1;
    padding: 15% 0.5rem 0 0.5rem;
    display: inline-block;
    position: relative;
    z-index: 1;
    text-align: left;
    color: #FFFFFF; }
    .home-wall .wall-content h2 {
      color: inherit;
      line-height: inherit;
      letter-spacing: 2px;
      font-size: 2rem;
      text-transform: uppercase;
      margin: 0;
      font-weight: 900; }
  @media only screen and (min-width: 40.063em) {
    .home-wall .wall-content h2 {
      font-size: 3rem; } }
  @media only screen and (min-width: 64.063em) {
    .home-wall .wall-content h2 {
      font-size: 4rem; } }

.intro-panel {
  margin-top: 1rem;
  padding-bottom: 1rem; }
  .intro-panel h2, .intro-panel p {
    margin: 0 1rem; }
  .intro-panel strong {
    color: #94C044; }

.tour-feature {
  margin-top: 1rem;
  padding-bottom: 1rem; }
  .tour-feature h2, .tour-feature p {
    margin: 0 1rem; }
  .tour-feature .royal-enfield {
    width: 100%;
    max-width: 400px; }
  .tour-feature .icon-big {
    width: 2rem;
    height: 2rem;
    float: left; }
  .tour-feature .tour-features {
    margin: 0; }
    .tour-feature .tour-features li {
      list-style: none; }
      .tour-feature .tour-features li .feature-item {
        margin-left: 2.5rem;
        font-size: 1.25rem; }
  .tour-feature .fuel-on-us {
    color: #94C044;
    font-weight: 700; }

.tour-list-panel h2, .tour-list-panel p, .tour-list-panel .list-card {
  margin: 0 1rem; }

.list-card .card-item {
  border: 1px solid #BABCBE;
  padding: 0.5rem;
  margin-bottom: 0.5rem; }
  .list-card .card-item h3 {
    display: inline-block;
    color: inherit;
    text-decoration: underline; }

.list-card .tour-dates .date-green {
  font-weight: 700;
  color: #94C044; }

@media only screen and (min-width: 40.063em) {
  .list-card .card-item {
    width: 49%;
    width: calc(50% - 5px); }
    .list-card .card-item.card-left {
      float: left; }
    .list-card .card-item.card-right {
      float: right; } }

.packages-list {
  list-style: none;
  padding: 0;
  margin: 0; }
  .packages-list .list-item {
    margin: 1rem;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative; }

.packages-panel-heading {
  margin: 0 1rem; }

.package-link {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 0.5rem;
  display: block;
  color: #FFFFFF; }
  .package-link:hover {
    color: #FFFFFF; }

.package-heading {
  /*color: scale-color($color-primary-blue, $lightness: 0, $saturation: 20%);*/
  color: #FFCC2A;
  font-weight: 400;
  margin: 0; }

.upcoming-packages {
  margin-top: 1rem;
  /* At 768px - custom media query range */ }
  .upcoming-packages .list-item {
    height: 200px; }
  @media only screen and (min-width: 40.063em) {
    .upcoming-packages .list-item {
      float: left;
      height: 260px; }
    .upcoming-packages .two-items {
      width: calc(50% - 1rem - 1rem); } }
  @media only screen and (min-width: 48em) {
    .upcoming-packages .list-item {
      float: left;
      height: 320px; }
    .upcoming-packages .two-items {
      width: calc(50% - 1rem - 1rem); } }

.featured-packages {
  margin-top: 1rem;
  padding-bottom: 1rem;
  position: relative; }
  .featured-packages .featured-list-item {
    height: 220px; }
    .featured-packages .featured-list-item:before {
      position: absolute;
      display: block;
      width: 100%;
      height: 100%; }
  .featured-packages .explore-link {
    margin: 0 1rem;
    font-size: 1.125rem;
    /* 18px/16px */ }
  @media only screen and (min-width: 40.063em) {
    .featured-packages .featured-list-item {
      float: left;
      width: 30%;
      /* Fallback */
      width: calc(33.33% - 1rem - 1rem); }
    .featured-packages .explore-link {
      position: absolute;
      bottom: 0;
      right: 1rem;
      margin: 0; } }
  @media only screen and (min-width: 64.063em) {
    .featured-packages .featured-list-item.large {
      width: 40%;
      /* Fallback */
      width: calc(42% - 1rem - 1rem);
      height: 280px; }
    .featured-packages .featured-list-item.small {
      width: 22%;
      /* Fallback */
      width: calc(29% - 1rem - 1rem); }
    .featured-packages .explore-link {
      margin-bottom: 3rem;
      font-size: 1.3125rem;
      /* 21px / 16px */ } }
